import toastr from 'toastr'
export function showMessageToastr (type, message) {
  toastr.options = {
    'closeButton': true,
    'debug': false,
    'progressBar': true,
    'positionClass': 'toast-top-right',
    'onclick': null,
    'showDuration': '400',
    'hideDuration': '1000',
    'timeOut': '3000',
    'extendedTimeOut': '1000',
    'showEasing': 'swing',
    'hideEasing': 'linear',
    'showMethod': 'fadeIn',
    'hideMethod': 'fadeOut'
  }
  toastr[type](message)
}
export function showMessageByAPICode (code) {
  var message, status
  switch (code) {
    case 200:
      message = 'Yu cu ca bn c x l thnh cng'
      status = 'success'
      break
    case 401:
      message = 'Yu cu ca bn x l tht bi, cha ng nhp vo h thng'
      status = 'error'
      break
    case 403:
      message = 'Yu cu ca bn x l tht bi, khng c quyn thay i d liu'
      status = 'error'
      break
    case 404:
      message = 'Yu cu ca bn x l tht bi, khng tm thy ti nguyn'
      status = 'error'
      break
    case 405:
      message = 'Yu cu khng c php x l'
      status = 'error'
      break
    case 409:
      message = 'Yu cu ca bn x l tht bi, xung t d liu'
      status = 'error'
      break
    case 500:
      message = 'Yu cu ca bn x l tht bi, li h thng'
      status = 'error'
      break
    default:
      message = 'Li kt ni'
      status = 'error'
      break
  }
  showMessageToastr(status, message)
}
export function currency (value) {
  if (value) {
    let moneyCur = (value / 1).toFixed(0).replace('.', ',')
    return moneyCur.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')
  }
  return ''
}



